import React from 'react';
import { Field as FieldType } from '@/types';

interface FieldProps {
  field: FieldType;
  onChange: (value: number | boolean | string) => void;
}

export const Field: React.FC<FieldProps> = ({ field, onChange }) => {
  const handleMinutesChange = (increment: number) => {
    const currentValue = Number(field.value);
    const newValue = currentValue + increment;
    
    // No permitir valores negativos y m√°ximo 240 minutos (4 horas)
    if (newValue >= 0 && newValue <= 240) {
      onChange(newValue);
    }
  };

  const renderField = () => {
    switch (field.type) {
      case 'minutes':
        if (field.id === 'fruits') {
          return (
            <div className="flex items-center space-x-2">
              <button
                onClick={() => {
                  const newValue = Number(field.value) - 1;
                  if (newValue >= 0) onChange(newValue);
                }}
                className={`px-3 py-1 rounded ${
                  Number(field.value) <= 0 
                    ? 'bg-[var(--color-disabled)] cursor-not-allowed' 
                    : 'bg-[var(--button-error)] text-white'
                }`}
                disabled={Number(field.value) <= 0}
              >
                -
              </button>
              <span className="w-16 text-center">{field.value} üçé</span>
              <button
                onClick={() => {
                  const newValue = Number(field.value) + 1;
                  if (newValue <= 10) onChange(newValue);
                }}
                className={`px-3 py-1 rounded ${
                  Number(field.value) >= 10 
                    ? 'bg-[var(--color-disabled)] cursor-not-allowed' 
                    : 'bg-[var(--button-success)] text-white'
                }`}
                disabled={Number(field.value) >= 10}
              >
                +
              </button>
            </div>
          );
        }
        return (
          <div className="flex items-center space-x-2">
            <button
              onClick={() => handleMinutesChange(-5)}
              className={`px-3 py-1 rounded ${
                Number(field.value) <= 0 
                  ? 'bg-[var(--color-disabled)] cursor-not-allowed' 
                  : 'bg-[var(--button-error)] text-white'
              }`}
              disabled={Number(field.value) <= 0}
            >
              -
            </button>
            <span className="w-16 text-center">{field.value} min</span>
            <button
              onClick={() => handleMinutesChange(5)}
              className={`px-3 py-1 rounded ${
                Number(field.value) >= 240 
                  ? 'bg-[var(--color-disabled)] cursor-not-allowed' 
                  : 'bg-[var(--button-success)] text-white'
              }`}
              disabled={Number(field.value) >= 240}
            >
              +
            </button>
          </div>
        );
      
      case 'boolean':
        return (
          <div className="flex items-center">
            <div
              onClick={() => onChange(!field.value)}
              className={`toggle-switch ${field.value ? 'active' : ''}`}
              role="switch"
              aria-checked={field.value}
              tabIndex={0}
            />
            <span className={`toggle-label ${field.value ? 'active' : ''}`}>
              {field.value ? 'Yes' : 'No'}
            </span>
          </div>
        );
      
      case 'text':
        return (
          <textarea
            value={field.value as string}
            onChange={(e) => onChange(e.target.value)}
            className="w-full p-2 border rounded mt-2"
            rows={4}
          />
        );
      
      case 'slider':
        const getSliderColor = (value: number) => {
          switch (value) {
            case 1:
              return 'var(--color-red)';
            case 2:
              return 'var(--color-orange)';
            case 3:
              return 'var(--color-yellow)';
            case 4:
              return 'var(--color-green)';
            case 5:
              return 'var(--color-green-bright)';
            default:
              return 'var(--color-red)';
          }
        };

        return (
          <div className="flex flex-col items-center">
            <div 
              className="slider-container relative"
              style={{
                background: getSliderColor(Number(field.value))
              }}
            >
              <div className="slider-track"></div>
              <input
                type="range"
                min="1"
                max="5"
                value={field.value as number}
                onChange={(e) => onChange(Number(e.target.value))}
                className="slider-input"
              />
              <div className="flex justify-between text-xs text-white mt-2 px-2 font-medium">
                <span>Low</span>
                <span>High</span>
              </div>
            </div>
          </div>
        );
    }
  };

  return (
    <div className={`mb-4 ${field.type === 'text' ? 'block' : 'grid grid-cols-2 gap-4 items-center'}`}>
      <label className="font-medium">
        {field.name}
        {field.required && <span className="text-red-500">*</span>}
      </label>
      {renderField()}
    </div>
  );
}; 